  .main-btn {
    padding: 15px 20px;
  }
}
.main-btn span {
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  margin-right: 20px;
  color: var(--main-btn-color);
}
.main-btn svg {
  fill: var(--main-btn-color);
}

.custom-select-container {
  position: relative;
}

.custom-select-container.is-disabled {
  opacity: 0.333;
}

.custom-select-opener {
  position: relative;
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  width: fit-content;
  min-width: 75px;
  padding: 8px;
  padding-right: 30px;
  cursor: pointer;
  color: #303030;
  background-color: transparent;
}
.custom-select-opener::before {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  content: "";
  width: 12px;
  height: 7px;
  background-image: url("../assets/img/icon-arrow-down.svg");
  background-size: contain;
  background-repeat: no-repeat;
  transition: transform var(--main-transition);
}

.custom-select-container select {
  visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
}

.custom-select-panel {
  position: absolute;
  height: 0;
  overflow: hidden;
  background-color: white;
  top: calc(100% + 10px);
  left: 0;
  z-index: 1;
  width: 100px;
  box-shadow: 0px 10px 20px rgba(42, 52, 79, 0.1);
}
.custom-select-panel::-webkit-scrollbar {
  width: 4px;
}

.custom-select-container.is-open .custom-select-panel {
  width: fit-content;
  min-width: 100px;
  height: fit-content;
  max-height: 150px;
  overflow-y: auto;
  z-index: 101;
}

.custom-select-option {
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  width: 100%;
  height: 40px;
  padding: 7px;
  cursor: pointer;
  white-space: nowrap;
  color: #303030;
  background-color: white;
  transition: all var(--main-transition);
}
.custom-select-option::before {
  display: none;
}
.custom-select-option:not(:last-child) {
  margin-bottom: 3px;
}

.custom-select-option.has-focus {
  color: var(--white);
  background-color: #303030;
}

.custom-select-optgroup > .custom-select-option {
  padding-left: 20px;
}

.form__content {
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 30px;
  margin-bottom: 40px;
}
.form__content fieldset:first-child {
  align-items: center;
}
.form__content fieldset:first-child input {
  width: calc(50% - 15px);
  padding: 10px 0;
  border: 0;
  border-bottom: 1px solid var(--white);
  background-color: var(--primary);
}
.form__content fieldset:first-child input:first-child {
  margin-right: auto;
}
@media (max-width: 635px) {
  .form__content fieldset:first-child input {
    width: 90%;
  }
  .form__content fieldset:first-child input:first-child {
    margin: 0;
    margin-bottom: 20px;
  }
}
@media (max-width: 534px) {
  .form__content fieldset:first-child input {
    width: 100%;
  }
}
.form__content fieldset:first-child input,
.form__content fieldset:first-child input::placeholder {
  font-weight: 500;
  font-size: 14px;
  line-height: 1;
  color: var(--white);
}
.form__content input[type=checkbox] {
  display: none;
}
.form__content input[type=checkbox] + label {
  position: relative;
  font-weight: 500;
  font-size: 14px;
  line-height: 1;
  padding-left: 24px;
  color: var(--white);
  cursor: pointer;
}
.form__content input[type=checkbox] + label::before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 16px;
  height: 16px;
  border: 1px solid var(--white);
  border-radius: 0;
  background-color: transparent;
  transition: border-color var(--main-transition);
}
.form__content input[type=checkbox]:checked + label::before {
  background-image: url("../assets/img/icon-form-check.svg");
  background-size: 10px;
  background-repeat: no-repeat;
  background-position: center;
}
.form__submit {
  border-color: var(--white);
}
.form__submit span {
  color: var(--white);
}
.form__submit svg {
  fill: var(--white);
}

.accordeon-height, .accordeon__body, .accordeon__header, .accordeon {
  height: 600px;
}
@media (max-width: 1440px) {
