  }
}
@media (max-width: 960px) {
  .accordeon-field-width, .accordeon__field {
    width: 100%;
  }
}

.accordeon {
  position: relative;
  overflow: hidden;
}
.accordeon__field {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--main-bg-color);
}
@media (max-width: 960px) {
  .accordeon__field {
    position: relative;
    flex-direction: column;
  }
}
.accordeon__field:not(:last-child) .accordeon__header {
  border-right: 1px solid var(--white);
}
@media (max-width: 960px) {
  .accordeon__field:not(:last-child) .accordeon__header {
    border-right: 0;
    border-bottom: 1px solid var(--white);
  }
}
.accordeon__field.active .accordeon__header {
  background-color: var(--accordeon-active-color);
  border-color: var(--accordeon-active-color);
}
.accordeon__field.active .accordeon__header span {
  color: var(--main-color);
}
@media (max-width: 960px) {
  .accordeon__field:not(.active) .accordeon__body {
    height: 0;
    padding: 0;
  }
  .accordeon__field.active .accordeon__body {
    animation: showaccordeonBody 0.7s ease-in forwards;
  }
  .accordeon__field.active .accordeon__header svg {
    transform: rotate(180deg);
    fill: var(--dark);
  }
}
.accordeon__header {
  position: relative;
  width: 100%;
  padding: 20px;
  background-color: var(--primary);
  z-index: 3;
  cursor: pointer;
  transition: background-color 0.5s ease-in, border 0.5s ease-in;
}
@media (max-width: 960px) {
  .accordeon__header {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-right: 40px;
  }
}
@media (max-width: 768px) {
  .accordeon__header {
    justify-content: flex-start;
  }
}
.accordeon__header span {
  font-weight: 500;
  font-size: 24px;
  text-align: center;
  white-space: nowrap;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-90deg);
  color: var(--white);
  transition: color 0.5s ease-in;
}
@media (max-width: 960px) {
  .accordeon__header span {
    position: relative;
    top: 0;
    left: 0;
    transform: none;
    font-size: 22px;
  }
}
@media (max-width: 768px) {
  .accordeon__header span {
    font-size: 20px;
  }
}
@media (max-width: 534px) {
  .accordeon__header span {
    font-size: 16px;
  }
}
.accordeon__header svg {
  display: none;
  margin-left: auto;
  fill: var(--white);
  transition: fill var(--main-transition), transform var(--main-transition);
}
@media (max-width: 960px) {
  .accordeon__header svg {
    display: block;
  }
}
.accordeon__title {
  font-weight: 400;
  font-size: 28px;
  margin: 0;
  margin-bottom: 15px;
}
.accordeon__body {
  display: flex;
  align-items: center;
  overflow: auto;
  background-color: var(--accordeon-active-color);
}
@media (max-width: 1140px) {
  .accordeon__body {
    flex-direction: column;
  }
}
@media (max-width: 960px) {
  .accordeon__body {
    flex-direction: row;
  }
}
@media (max-width: 768px) {
  .accordeon__body {
    flex-direction: column;
  }
}
.accordeon__body-img-box {
  width: 50%;
