  max-height: 600px;
  padding: 20px;
}
@media (max-width: 1140px) {
  .accordeon__body-img-box {
    width: 100%;
    max-height: 450px;
    margin-right: 0;
  }
}
@media (max-width: 960px) {
  .accordeon__body-img-box {
    width: 50%;
  }
}
@media (max-width: 768px) {
  .accordeon__body-img-box {
    width: 100%;
  }
}
.accordeon__body img {
  display: block;
  width: 100%;
  height: 100%;
  max-height: 600px;
  object-fit: contain;
}
@media (max-width: 1140px) {
  .accordeon__body img {
    max-height: 400px;
    margin-bottom: 20px;
