  }
}
@media (max-width: 960px) {
  .accordeon__body img {
    margin-bottom: 0;
  }
}
@media (max-width: 768px) {
  .accordeon__body img {
    margin-bottom: 20px;
    object-fit: cover;
  }
}
.accordeon__body-content {
  width: 50%;
  padding: 20px;
}
@media (max-width: 1140px) {
  .accordeon__body-content {
    width: 100%;
  }
}
@media (max-width: 960px) {
  .accordeon__body-content {
    width: 50%;
  }
}
@media (max-width: 768px) {
  .accordeon__body-content {
    width: 100%;
  }
}

