.callback__side-overlay-text span {
  font-weight: 700;
  font-size: clamp(50px, 9vw, 214px);
  line-height: 80%;
  text-align: center;
  color: #40BDC5;
}
@media (max-width: 1140px) {
  .callback__side-overlay-text span {
    font-size: 60px;
  }
}
.callback__side svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60%;
  height: 60%;
  fill: var(--white);
  z-index: 2;
}
@media (max-width: 1140px) {
  .callback__side svg {
    position: static;
    width: 165px;
    height: auto;
    transform: initial;
    order: 1;
  }
}

.map {
  position: relative;
  z-index: 3;
  width: 100%;
  max-width: 2400px;
  height: 500px;
  margin: 0 auto;
  background-color: var(--dark);
  background-image: url("../assets/img/map-static@1x.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
@media (max-width: 960px) {
  .map {
    height: 400px;
  }
}
@media (max-width: 534px) {
  .map {
    height: 300px;
  }
}

#yandex_map {
  width: 100%;
  max-width: 2400px;
  height: 100%;
  margin: 0 auto;
}
#yandex_map .zoom-btns {
  display: flex;
  align-items: center;
  position: absolute;
  top: -440px;
  left: -75px;
}
@media (max-width: 960px) {
  #yandex_map .zoom-btns {
    top: -340px;
  }
}
@media (max-width: 534px) {
  #yandex_map .zoom-btns {
    top: -240px;
  }
}
#yandex_map .zoom-btns .zoom-btn {
  width: 40px;
  height: 40px;
  margin: 0 5px;
  background-color: var(--primary);
  cursor: pointer;
  transition: background-color var(--main-transition);
}
#yandex_map .zoom-btns .zoom-btn svg {
  fill: var(--white);
}
#yandex_map .zoom-btns .zoom-btn:hover {
  opacity: 0.8;
}
#yandex_map .zoom-btns .zoom-btn:active {
  background-color: var(--primary);
}

.main-footer {
  padding: 30px 0;
  background-color: var(--main-footer-bg);
}
.main-footer__container {
  align-items: center;
}
@media (max-width: 768px) {
  .main-footer__container {
    flex-direction: column;
  }
  .main-footer__container > a {
    margin-bottom: 40px;
  }
}
.main-footer__info {
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  padding: 0 25px;
}
.main-footer__info a {
  font-weight: 500;
  font-size: 18px;
  line-height: 20px;
  margin: 0 30px;
  color: var(--white);
  /*svg {
    margin-right: 12px;
    fill: var(--white);
  }*/
}
@media (max-width: 960px) {
  .main-footer__info {
    flex-direction: column;
  }
  .main-footer__info a:first-child {
    margin-bottom: 15px;
  }
}
@media (max-width: 768px) {
  .main-footer__info {
    width: 100%;
    padding-bottom: 30px;
    margin-bottom: 30px;
    border-bottom: 1px solid var(--white);
  }
}